/* Main Projects Container */
.projects {
  padding: 50px;
  background-color: #f4f4f4;
  display: flex; /* Use Flexbox for centering */
  justify-content: center; /* Center horizontally */
  align-items: center; /* Center vertically */
  flex-direction: column; /* Align content in column format */
}

/* Projects Heading */
.projects h1 {
  font-size: 2.5rem;
  color: #333;
  text-align: center; /* Center the heading */
}

/* Project Grid Container */
.project-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 20px;
  max-width: 1200px; /* Set a max-width to keep the grid compact */
  margin: 0 auto; /* Center the grid container */
}

/* Individual Project Container */
.project-container {
  border: 1px solid #000; /* 4px thick border */
  padding: 15px; /* 10px space around the project */
}

/* Project Card Style */
.project {
  background-color: white;
  padding: 20px;
  border: 1px solid #ddd;
  border-radius: 10px;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
  transition: box-shadow 0.3s ease;
}

/* Project Hover Effect */
.project:hover {
  box-shadow: 0px 6px 12px rgba(0, 0, 0, 0.15);
}

/* Project Image Style */
.project img {
  max-width: 100%;
  height: auto;
  border-radius: 10px;
  margin-bottom: 15px;
}

/* Project Title Style */
.project h3 {
  font-size: 1.5rem;
  color: #333;
}

/* Project Description Style */
.project p {
  font-size: 1.1rem;
  color: #555;
}
